
import { useEffect, useMemo, useCallback, useState } from 'react';

import { HashLink } from 'react-router-hash-link';
import BBB_Logo from '../assets/BOBBYLOGO_SVG.png';




const Sidebar = ({children}) => {
 
    
    const[isOpen ,setIsOpen] = useState(false);
    const toggle = () => setIsOpen (!isOpen);
    const menuItem=[
        {
            path:"/#home",
            name:"HOME",
     
        },
        {
            path:"/#section1",
            name:"ABOUT",
        
        },
        {
            path:"/#section2",
            name:"BOOOST",
       
        },
        {
            path:"/#section3",
            name:"SOCIALS",
       
        },
       
    ]
    return (
        <div className="container">
        <div  className="sidebarM" style={{width: isOpen ? "80%" : "", height: isOpen ? "90vh" : "80px", background:isOpen ? "rgba(0, 0, 0, 0.80)" : "", }}>
               <div className="top_section" style={{width: isOpen ? "100%" : "", height: isOpen ? "25%" : "", justifyContent: isOpen ? "center" : ""}}>
                  <img src={BBB_Logo} alt="" className='logo' onClick={toggle} style={{width: isOpen ? "250px" : ""}} />
                  
               </div>
               
               {
                   menuItem.map((item, index)=>(
                       <HashLink smooth to={item.path} key={index} className="link NavigationButtons" activeclassName="active" style={{display: isOpen ? "block" : "none", background: isOpen ? "#169000": "", color: isOpen ? "black" : ""}}>
                           <div className="icon">{item.icon}</div>
                           <div className="link_text">{item.name}</div>
                       </HashLink>
                   ))
               }
               
           </div>

           
           <main >{children}</main>
        </div>
    );
};

export default Sidebar;