import React from "react";

import Slider from "react-slick";
import { useGlitch } from 'react-powerglitch'

function SimpleSlider() {
  const glitchBooost = useGlitch();

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };
  return (
    <div className="slider-container">
      <Slider {...settings} className="SliderMain">
        <div className="slidersBackground"> 
          
          <ol style={{width:'100%'}}>
            <li>The OG buy bot is back</li>
            <li>faster, better</li>
            <li>and with <span ref={glitchBooost.ref} style={{fontSize:'4rem'}}>booost!</span></li>
          </ol>
        </div>
        <div className="slidersBackground">
        <h1 style={{color:'white', fontSize:'3rem', color:'#00FF00'}}>WHAT'S NEW?</h1>
        </div>
        <div className="slidersBackground">
        <h1 style={{color:'white', fontSize:'3rem', color:'#00FF00'}}>WHAT'S NEW?</h1>
        </div>
       
      </Slider>
    </div>
  );
}

export default SimpleSlider;